"use client"

import { useState, useEffect } from "react"
import { useRouter } from "next/navigation"
import Link from "next/link"
import Header from "@/components/header"
import Footer from "@/components/footer"
import { Card } from "@/components/ui/card"
import { Input } from "@/components/ui/input"
import { Button } from "@/components/ui/button"
import { 
  User, 
  Mail, 
  Lock, 
  CheckCircle2, 
  AlertCircle, 
  ShieldCheck, 
  Settings 
} from "lucide-react"
import { postToApi } from "@/lib/data"

export default function ProfilePage() {
  const [isLoading, setIsLoading] = useState(false)
  const [msg, setMsg] = useState({ type: "", text: "" })
  const [user, setUser] = useState<any>(null)
  const [formData, setFormData] = useState({
    name: "",
    password: "",
    confirm_password: ""
  })
  const router = useRouter()

  useEffect(() => {
    // Pastikan kode ini jalan di client
    const info = localStorage.getItem("user_info")
    if (!info) {
      router.push("/login")
    } else {
      try {
        const parsed = JSON.parse(info)
        setUser(parsed)
        setFormData(prev => ({ ...prev, name: parsed.name }))
      } catch (e) {
        console.error("Gagal parse data user")
        router.push("/login")
      }
    }
  }, [router])

  const handleUpdate = async (e: React.FormEvent) => {
    e.preventDefault()
    setIsLoading(true)
    setMsg({ type: "", text: "" })

    // Validasi Password jika diisi
    if (formData.password && formData.password !== formData.confirm_password) {
      setMsg({ type: "error", text: "Konfirmasi kata sandi tidak cocok!" })
      setIsLoading(false)
      return
    }

    // Hit API Laravel untuk update profil
    const res = await postToApi("/user/update", formData)

    if (res.success) {
      setMsg({ type: "success", text: "Profil berhasil diperbarui!" })
      // Update info di local storage agar nama di header juga berubah
      const updatedUser = { ...user, name: formData.name }
      localStorage.setItem("user_info", JSON.stringify(updatedUser))
      setUser(updatedUser)
      // Kosongkan field password setelah sukses
      setFormData(prev => ({ ...prev, password: "", confirm_password: "" }))
    } else {
      setMsg({ type: "error", text: res.message || "Gagal memperbarui profil." })
    }
    setIsLoading(false)
  }

  if (!user) return null

  return (
    <>
      <Header />
      <main className="min-h-screen bg-[#f4e8d1] py-16 px-6 font-sans">
        <div className="max-w-4xl mx-auto grid grid-cols-1 lg:grid-cols-3 gap-10">
          
          {/* Sidebar Profil Card */}
          <div className="lg:col-span-1 space-y-6">
            <Card className="p-8 bg-white rounded-[40px] border-none shadow-xl text-center overflow-hidden relative">
              <div className="absolute top-0 left-0 w-full h-24 bg-[#a64029]"></div>
              <div className="relative pt-6">
                <div className="w-24 h-24 bg-[#dfaf2b] rounded-3xl border-4 border-white mx-auto flex items-center justify-center font-black text-3xl text-[#3b2f2f] shadow-lg">
                  {user.name?.[0]?.toUpperCase() || "U"}
                </div>
                <h2 className="mt-4 font-serif text-2xl font-bold text-[#3b2f2f] truncate px-2">{user.name}</h2>
                <p className="text-[10px] font-black text-[#4e5b31] uppercase tracking-[0.2em] mt-2 bg-[#4e5b31]/10 py-1.5 px-4 rounded-full inline-block">
                  Member Aktif
                </p>
              </div>
              <div className="mt-8 pt-8 border-t border-gray-100 space-y-4 text-left">
                <div className="flex items-center gap-3 text-gray-500">
                  <div className="w-8 h-8 bg-gray-50 rounded-lg flex items-center justify-center">
                    <Mail size={14} />
                  </div>
                  <span className="text-xs font-bold truncate">{user.email}</span>
                </div>
                <div className="flex items-center gap-3 text-gray-500">
                  <div className="w-8 h-8 bg-gray-50 rounded-lg flex items-center justify-center">
                    <ShieldCheck size={14} />
                  </div>
                  <span className="text-xs font-bold italic">Akun Terverifikasi</span>
                </div>
              </div>
            </Card>
          </div>

          {/* Main Form Card */}
          <div className="lg:col-span-2">
            <Card className="p-8 md:p-12 bg-white rounded-[40px] border-none shadow-xl">
              <div className="flex items-center gap-4 mb-10">
                <div className="p-4 bg-[#a64029]/10 rounded-2xl text-[#a64029]">
                  <Settings size={28} />
                </div>
                <div>
                  <h2 className="text-2xl font-serif font-bold text-[#3b2f2f]">Pengaturan Akun</h2>
                  <p className="text-sm text-gray-400 font-medium">Perbarui informasi dasar dan keamanan Anda.</p>
                </div>
              </div>

              {msg.text && (
                <div className={`mb-8 p-5 rounded-2xl flex items-center gap-3 border transition-all animate-in fade-in slide-in-from-top-2 ${msg.type === 'success' ? 'bg-green-50 border-green-200 text-green-700' : 'bg-red-50 border-red-200 text-red-700'}`}>
                  {msg.type === 'success' ? <CheckCircle2 size={20} /> : <AlertCircle size={20} />}
                  <p className="font-bold text-sm">{msg.text}</p>
                </div>
              )}

              <form onSubmit={handleUpdate} className="space-y-8">
                {/* Field Nama */}
                <div className="space-y-2">
                  <label className="text-[10px] font-black text-gray-400 uppercase tracking-[0.2em] ml-2">Nama Lengkap Anda</label>
                  <div className="relative">
                    <User className="absolute left-4 top-4 w-5 h-5 text-gray-300" />
                    <Input 
                      className="pl-12 py-8 border-gray-100 rounded-2xl bg-gray-50/50 focus:ring-2 focus:ring-[#a64029] focus:bg-white transition-all text-sm font-bold text-[#3b2f2f]" 
                      value={formData.name} 
                      onChange={(e) => setFormData({...formData, name: e.target.value})} 
                      required 
                    />
                  </div>
                </div>

                <div className="h-px bg-gray-50 w-full"></div>

                {/* Field Password */}
                <div className="grid grid-cols-1 md:grid-cols-2 gap-6">
                  <div className="space-y-2">
                    <label className="text-[10px] font-black text-gray-400 uppercase tracking-[0.2em] ml-2">Ganti Kata Sandi</label>
                    <div className="relative">
                      <Lock className="absolute left-4 top-4 w-5 h-5 text-gray-300" />
                      <Input 
                        type="password" 
                        placeholder="••••••••" 
                        className="pl-12 py-8 border-gray-100 rounded-2xl bg-gray-50/50 focus:ring-2 focus:ring-[#a64029] focus:bg-white transition-all" 
                        value={formData.password}
                        onChange={(e) => setFormData({...formData, password: e.target.value})}
                      />
                    </div>
                  </div>
                  <div className="space-y-2">
                    <label className="text-[10px] font-black text-gray-400 uppercase tracking-[0.2em] ml-2">Konfirmasi Sandi</label>
                    <div className="relative">
                      <CheckCircle2 className="absolute left-4 top-4 w-5 h-5 text-gray-300" />
                      <Input 
                        type="password" 
                        placeholder="••••••••" 
                        className="pl-12 py-8 border-gray-100 rounded-2xl bg-gray-50/50 focus:ring-2 focus:ring-[#a64029] focus:bg-white transition-all" 
                        value={formData.confirm_password}
                        onChange={(e) => setFormData({...formData, confirm_password: e.target.value})}
                      />
                    </div>
                  </div>
                </div>

                <div className="pt-4">
                  <Button 
                    disabled={isLoading} 
                    type="submit" 
                    className="w-full py-9 bg-[#a64029] hover:bg-[#85311e] text-white font-black rounded-3xl text-sm uppercase tracking-[0.2em] shadow-2xl shadow-[#a64029]/30 transition-all flex items-center justify-center gap-3 disabled:opacity-50"
                  >
                    {isLoading ? (
                      <div className="h-5 w-5 animate-spin rounded-full border-2 border-white border-t-transparent"></div>
                    ) : (
                      "Simpan Perubahan Profil"
                    )}
                  </Button>
                </div>
              </form>
            </Card>
          </div>

        </div>
      </main>
      <Footer />
    </>
  )
}